import React, { useCallback, useEffect, useState } from 'react';
import * as T from './types';
import * as S from './styles';
import { colors } from '../../shared';

export const Colors: React.FC<T.ColorsProps> = () => {
  const [colorsObject, setColorsObject] = useState([]);

  const keys = Object.keys(colors);

  useEffect(() => {
    keys.forEach(key => {
      const value = colors[key];
      setColorsObject(currentList => [...currentList, { key, value }]);
    });

    colorsObject.map(color => {
      const keys = Object.keys(color.value);
      keys.forEach(key => {
        const hex = color.value[key];
        setColorsObject(currentList => [...currentList, hex]);
      });
    });

    return () => {};
  }, []);

  useEffect(() => {
    // colorsObject.map(color => {
    //   const keys = Object.keys(color.value);
    //   keys.forEach(key => {
    //     const hex = color.value[key];
    //     setColorsObject(currentList => [...currentList, hex]);
    //   });
    // });
    return () => {};
  }, []);

  console.log(colorsObject);
  return (
    <S.Wrapper>
      {/* {keys.map(color => (
        <div key={color}>{color}</div>
      ))} */}
    </S.Wrapper>
  );
};
